<template>
  <v-card flat width="100%" height="100%">
    <svg v-if="loaded" width="100%" height="95%" :viewBox="viewBox">
      <image :href="img_src" width="100%" height="100%" />
      <polygon
        v-for="(item, idx) in items"
        :key="'bbox ' + idx"
        stroke="#009E73FF"
        :points="set_poly(item.boundingBox)"
        fill="transparent"
        :content="
          item.confidence != null
            ? `${item.text} (${item.confidence.toFixed(3)})`
            : `${item.text}`
        "
        v-tippy="{
          animation: 'fade',
          arrow: true,
          size: 'large',
        }"
      />
    </svg>
    <div
      v-else
      class="text-center"
      style="
        height: 95%;
        display: flex;
        justify-content: center;
        align-items: center;
      "
    >
      <v-progress-circular
        v-if="!this.start"
        indeterminate
        color="primary"
        :size="100"
        :width="10"
      ></v-progress-circular>
    </div>
  </v-card>
</template>

<script>
import Vue from "vue";
import VueTippy from "vue-tippy";

Vue.use(VueTippy);

export default {
  props: {
    img_src: String,
    items: Array,
    alpha_w: Number,
    alpha_h: Number,
    width: Number,
    height: Number,
    loaded: Boolean,
    start: Boolean,
  },

  computed: {
    viewBox() {
      return "0 0 " + this.alpha_w + " " + this.alpha_h;
    },
  },

  methods: {
    set_poly(bbox) {
      var polygon = "";
      for (var coord of bbox.vertices) {
        polygon += this.transX(coord.x) + "," + this.transY(coord.y) + " ";
      }
      return polygon;
    },

    transX(x) {
      return (this.alpha_w * x) / this.width;
    },

    transY(y) {
      return (this.alpha_h * y) / this.height;
    },
  },
};
</script>
