<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <v-btn icon @click="dialogVisible = true">
        <v-icon>mdi-key</v-icon>
      </v-btn>
      <v-spacer />
      <v-toolbar-title>
        <div class="font-weight-bold text-h5">AIStationReader</div>
      </v-toolbar-title>
      <v-spacer />
      <v-btn icon @click="redirectToStation">
        <v-icon>mdi-gas-station</v-icon>
      </v-btn>
    </v-app-bar>

    <v-dialog v-model="dialogVisible" max-width="500px">
      <template v-slot:activator>
        <v-btn color="primary" @click="openDialog">Open Dialog</v-btn>
      </template>
      <v-card class="pa-5">
        <v-card-title class="headline">Enter API key</v-card-title>
        <v-card-text>
          <v-text-field v-model="textInput" label="Text Input"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="primary" @click="submitText">Submit</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-main>
      <v-card flat class="d-flex justify-center" width="100vw">
        <v-card flat max-width="2000px" width="95vw">
          <v-row class="pa-5">
            <v-col cols="6">
              <v-card flat class="ma-0 pa-0" height="calc(90vh - 100px)">
                <v-row>
                  <v-col>
                    <h3 class="mt-5 pl-4 pb-6">OCR 결과 (이미지)</h3>
                  </v-col>
                </v-row>
                <box-result
                  :img_src="img_src"
                  :items="items"
                  :alpha_w="alpha_w"
                  :alpha_h="alpha_h"
                  :width="imgWidth"
                  :height="imgHeight"
                  :loaded="loaded"
                  :start="start"
                />
              </v-card>
            </v-col>
            <v-col cols="6">
              <v-card flat class="d-flex flex-column justify-space-between">
                <v-row>
                  <v-col>
                    <h3 class="mt-5 pl-4 pb-2">이미지 파일</h3>
                    <v-file-input
                      class="px-5"
                      accept="image/*, .pdf"
                      label="이미지 선택"
                      outlined
                      dense
                      ref="image"
                      v-model="file"
                      @change="onFileChange"
                    ></v-file-input>
                    <h3 class="mt-5 pl-4 pb-2">OCR 결과 (JSON)</h3>
                    <v-card
                      class="overflow-y-auto"
                      flat
                      height="calc(90vh - 350px)"
                    >
                      <v-card-text>
                        <div v-if="loaded" class="scrollable-content pl-4">
                          <tree-view
                            :data="ocr_result"
                            :options="{ maxDepth: 5 }"
                          ></tree-view>
                        </div>
                        <div
                          v-else
                          class="text-center"
                          style="
                            height: 50vh;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                          "
                        >
                          <v-progress-circular
                            v-if="!this.start"
                            indeterminate
                            color="primary"
                            :size="50"
                            :width="5"
                          ></v-progress-circular>
                        </div>
                      </v-card-text>
                    </v-card>

                    <v-btn
                      class="mt-7"
                      align-center
                      width="100%"
                      outlined
                      color="indigo"
                      @click="download"
                      >DOWNLOAD</v-btn
                    >
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
      </v-card>
    </v-main>
  </v-app>
</template>

<script>
import BoxResult from "./components/BoxResult.vue";
import axios from "axios";
import Vue from "vue";

import TreeView from "vue-json-tree-view";
Vue.use(TreeView);

export default {
  components: {
    BoxResult,
  },
  data() {
    return {
      dialogVisible: true,
      img_src: null,
      imgWidth: null,
      imgHeight: null,
      alpha_w: null,
      alpha_h: null,
      ratio: 500,
      loaded: false,
      start: true,

      file: "",
      items: [],

      api_key: "",
      url: "/v1/document-ai/ocr",
    };
  },
  methods: {
    async onFileChange() {
      this.loaded = false;
      this.start = false;
      const file = this.file;
      const reader = new FileReader();
      reader.onload = (e) => {
        this.img_src = e.target.result;

        let img = new Image();
        img.src = e.target.result;
        img.onload = () => {
          this.imgWidth = img.width;
          this.imgHeight = img.height;
          this.alpha_w =
            (this.imgWidth / Math.max(this.imgWidth, this.imgHeight)) *
            this.ratio;
          this.alpha_h =
            (this.imgHeight / Math.max(this.imgWidth, this.imgHeight)) *
            this.ratio;
        };
      };

      var fd = new FormData();

      if (this.file != "") {
        fd.append("image", this.file);
      }

      const headers = {
        Authorization: "Bearer " + this.api_key,
      };

      try {
        axios.defaults.withCredentials = true;
        const response = await axios.post(this.url, fd, { headers });
        reader.readAsDataURL(file);

        this.ocr_result = response.data;
        this.items = this.ocr_result.pages[0].words;
        // 응답 데이터 처리 로직 추가
      } catch (error) {
        window.alert(
          "Sorry... Something is wrong.\nPlease check your API key again."
        );
        this.dialogVisible = true;
        // 에러 처리 로직 추가
      }
      this.loaded = true;
    },

    download() {
      if (this.loaded) {
        if (confirm("결과를 저장하시겠습니까?")) {
          var json = JSON.stringify(this.ocr_result, null, "  ");

          const filename = "OCR_result.json";
          const element = document.createElement("a");
          element.setAttribute(
            "href",
            "data:text/json;charset=utf-8, " + encodeURIComponent(json)
          );
          element.setAttribute("download", filename);
          document.body.appendChild(element);
          element.click();
        }
      }
    },

    openDialog() {
      this.dialogVisible = true;
    },
    closeDialog() {
      this.dialogVisible = false;
      this.textInput = "";
    },
    submitText() {
      // Do something with the submitted text
      this.api_key = this.textInput;
      this.closeDialog();
    },
    redirectToStation() {
      window.open("https://console.upstage.ai/", "_blank");
    },
  },
};
</script>

<style scoped>
.border {
  border: 1px solid black;
  height: 100vh; /* 화면을 꽉 채우도록 설정 */
}

.card-container {
  overflow: hidden;
}

.scrollable-content {
  overflow-y: auto;
  height: 100%;
}
</style>